<template>
  <div id="app">
    <Header id="header" v-if="showHeaderAndFooter"></Header>
    <div id="main">
      <router-view/>
    </div>
    <Footer id="footer" v-if="showHeaderAndFooter"></Footer>
  </div>
</template>

<script>
import Header from '../src/components/header'
import Footer from '../src/components/footer'
import Vue from 'vue'
import {mapGetters} from 'vuex'

export default {
  name: 'App',
  components: {
    Header,
    Footer
  },
  data(){
    return {
      eventBus: new Vue()
    }
  },
  computed: {
    ...mapGetters(['showHeaderAndFooter'])
  }
}
</script>

<style lang="less">
  @import "../src/assets/base";

  #app {
    height: 100vh;
  }
  body.night {
    background-color: @nightBackgroundColor;
  }
  #app {
    display: grid;
    grid-template-columns: 12% auto 12%;
    grid-template-rows: auto 1fr auto;
    grid-template-areas: "header header header"
                         "main   main   main   "
                         "footer footer footer";
    align-self: center;
    align-content: stretch;
    #header {
      grid-area: header;
      padding-left: 12%;
      padding-right: 12%;
    }
    #main {
      grid-area: main;
      justify-self: stretch;
      padding-left: 0;
      padding-right: 0;
    }
    #footer {
      grid-area: footer;
      padding-left: 12%;
      padding-right: 12%;
    }
  }
</style>



