import Vue from 'vue'
import Vuex from 'vuex'
import auth from './modules/auth'
import blog from './modules/blog'

Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    showHeaderAndFooter: true
  },
  getters: {
    showHeaderAndFooter: state => state.showHeaderAndFooter
  },
  mutations: {
    show (state) {
      state.showHeaderAndFooter = true
    },
    hide (state) {
      state.showHeaderAndFooter = false
    }
  },
  modules: {
    auth,
    blog
  }
})



