<template>
  <div class="header-wrapper">
    <header class="header" :class="classes">
      <template v-if="!this.isLogin">
        <router-link class="toIndex" to="/">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-bulb"></use>
          </svg>
          <h1>LET'S SHARE</h1>
        </router-link>
        <div class="width-menu">
          <router-link to="/create">
            <div class="shake-little">
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-edit"></use>
              </svg>
            </div>
          </router-link>
          <div class="login-and-register">
            <router-link to="/login">登录</router-link>
            <router-link to="/register">注册</router-link>
          </div>
        </div>
        <div class="narrow-menu">
          <el-dropdown trigger="click">
            <span class="el-dropdown-link">
              <svg class="icon menu" aria-hidden="true">
                <use xlink:href="#icon-menu"></use>
              </svg>
            </span>
            <el-dropdown-menu slot="dropdown" >
              <el-dropdown-item style="text-align:center;">
                <router-link to="/login" style="color: #999;">
                  登录
                </router-link>
              </el-dropdown-item>
              <el-dropdown-item style="text-align:center;">
                <router-link to="/register" style="color: #999;">
                  注册
                </router-link>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </template>
      <template v-if="this.isLogin">
        <router-link class="toIndex" to="/">
          <svg class="icon " aria-hidden="true">
            <use xlink:href="#icon-bulb"></use>
          </svg>
          <h1>LET'S SHARE</h1>
        </router-link>
        <router-link to="/create">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-edit"></use>
            </svg>
        </router-link>
        <div class="avatar-wrapper">
          <div class="wide-avatar">
            <img id="avatar" src="http://pf5avane2.bkt.clouddn.com/6.jpg" alt="">
            <ul class="more">
              <li>
                <router-link to="/my">
                  <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-profile"></use>
                  </svg>
                  我的
                </router-link>
              </li>
              <li @click="onLogout">
                <router-link to="/">
                  <svg class="icon logout" aria-hidden="true">
                    <use xlink:href="#icon-logout"></use>
                  </svg>
                  注销
                </router-link>
              </li>
            </ul>
          </div>
          <div class="narrow-avatar">
            <el-dropdown trigger="click" @command="handleCommand">
            <span class="el-dropdown-link">
              <img id="avatar" src="http://pf5avane2.bkt.clouddn.com/6.jpg" alt="">
            </span>
              <el-dropdown-menu slot="dropdown" >
                <el-dropdown-item style="text-align:center;">
                  <router-link to="/my" style="color: #999;display: flex;align-items: center">
                    <svg class="icon profile" aria-hidden="true">
                      <use xlink:href="#icon-profile"></use>
                    </svg>
                    我的
                  </router-link>
                </el-dropdown-item>
                <el-dropdown-item style="text-align:center;" command="logout">
                  <router-link to="/" style="color: #999;display: flex;align-items: center" @click="onLogout">
                    <svg class="icon logout" aria-hidden="true">
                      <use xlink:href="#icon-logout"></use>
                    </svg>
                    注销
                  </router-link>
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
      </template>
    </header>
  </div>
</template>

<script>
  import '../assets/svg.js'
  import { mapGetters, mapActions} from 'vuex'
  import { default as swal } from 'sweetalert2'

  export default {
    name: 'HelloWorld',
    data () {
      return {

      }
    },
    computed: {
      classes(){
        return {
          [`isLogin`]: this.isLogin,
          [`noLogin`]: !this.isLogin,
          [`clearfix`]: true
        }
      },
      ...mapGetters(['user','isLogin'])
    },
    created(){
      this.checkLogin()
    },
    methods: {
      ...mapActions(['checkLogin','logout']),
      onLogout(){
        this.logout().then(()=>{
          swal({
            title: '注销成功',
            text: '',
            type: 'success',
            confirmButtonText: '确认'
          })
        })
      },
      randomPicNum(){
        let num = 1 + Math.round(Math.random()*12)
        return num
      },
      handleCommand(command) {
        if(command === 'logout'){
          this.onLogout()
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  @import "../assets/base";
  @import "../assets/shake.css";

  .header-wrapper {
    height: 80px;
  }
  .narrow-menu {
    display: none;
  }
  .header {
    background-color: @themeColor;
    color: #fff;
    position: fixed;
    z-index: 1;
    width: 100%;
    left:0;
    right:0;
    .setFont {
      button {
        background-color: transparent;
        height: 34px;
        display: inline-flex;
        align-items:center;
        padding: 0;
        border: none;
        margin-right: 10px;
      }
    }
  }
  .header.night {
    background-color: @nightBackgroundColor;
    border-bottom: 1px solid #2f2f2f;
  }
  .header.noLogin {
    display: flex;
    padding: 10px 12%;
    align-items: center;
    .toIndex {
      display: flex;
      align-items: center;
      flex: 1;
      text-decoration: none;
      .icon {
        fill: #fff;
      }
      h1 {
        margin-left: 10px;
        color: #fff;
        font-size: 28px;
      }
    }
    .icon {
      fill: #fff;
      height: 2em;
      width: 2em;
    }
    .login-and-register {
      display: flex;
      align-items: center;
      margin-left: 1em;
      a {
        border: 1px solid #fff;
        color: #fff;
        padding: .4em 1.5em;
        border-radius: 5px;
        transition: 0.2s;
        &:hover {
          background-color: rgba(250,250,250, 0.3);
        }
        &:first-child {
          margin-right: 1em;
          border: none;
        }
      }
    }
  }
  .header.isLogin{
    display: flex;
    padding: 0 12%;
    align-items: center;
    .toIndex {
      display: flex;
      align-items: center;
      flex: 1;
      text-decoration: none;
      h1 {
        margin-left: 10px;
        color: #fff;
        font-size: 28px;
      }
      .icon {
        fill: #fff;
      }
    }
    .icon {
      height: 2em;
      width: 2em;
      fill: #fff;
    }
    .avatar-wrapper {
      position: relative;
      //border: 1px solid red;
      padding: 10px 15px;
      display: flex;
      align-items: center;
      justify-content: center;
      &:hover {
        .more {
          display: block;
        }
      }
      #avatar {
        height: 40px;
        width: 40px;
        border-radius: 50%;
      }
      .more {
        display: none;
        list-style: none;
        position: absolute;
        right: 0;
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        width: 80px;
        border: 1px solid #f0f0f0;
        border-radius: 3px;
        overflow: hidden;
        >li {
          padding: .3em 0;
          background-color: #fff;
          &:first-child {
            border-bottom: 1px solid #f0f0f0;
          }
          &:hover {
            background-color: rgba(250,250,250,0.9);
          }
          a {
            color: #999;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 5px 0;
            font-size: 14px;
          }
          .icon {
            fill: #0fa93a;
            height: 1.5em;
            width: 1.5em;
            margin-right: .5em;
          }
          .logout {
            fill: #ff4949;
          }
        }
      }
    }
  }
  .width-menu {
    display: flex;
    align-items: center;
  }
  .narrow-avatar {
    display: none;
  }
  
  @media (max-width: 768px) {
    .header-wrapper {
      header.header {
        padding: 3vw;
      }
      .header.isLogin {
        .avatar-wrapper {
          padding-top: 0;
          padding-bottom: 0;
        }
      }
    }
    .width-menu {
      display: none;
    }
    .narrow-menu {
      display: block;
      a {
        color: #000;
      }
      .icon.menu {
        height: 10vw;
        width: 10vw;
      }
    }
    .wide-avatar {
      display: none;
    }
    .narrow-avatar {
      display: block;
    }
  }

</style>



